.qv-object-advanced-analytics-toolbox * {
  font-family: 'Noto Sans', sans-serif;
  font-size: 12px;
  color: #595959;
}
.qv-object-advanced-analytics-toolbox h2 {
  display: block;
  font-size: 16px;
  -webkit-margin-before: 0.83em;
  -webkit-margin-after: 0.83em;
  -webkit-margin-start: 0px;
  -webkit-margin-end: 0px;
  font-weight: bold;
  margin-top: 8px;
  margin-bottom: 2px;
}
.qv-object-advanced-analytics-toolbox table.simple {
  border-spacing: 0;
  border: 1px solid #CCCCCC;
  border-radius: 3px;
  width: 100%;
}
.qv-object-advanced-analytics-toolbox table.simple thead {
  display: table-header-group;
  vertical-align: middle;
  border-color: inherit;
}
.qv-object-advanced-analytics-toolbox table.simple tbody tr:nth-child(2n - 1) {
  background-color: #F2F2F2;
}
.qv-object-advanced-analytics-toolbox table.simple docs.css:67 table tr {
  box-sizing: border-box;
}
.qv-object-advanced-analytics-toolbox table.simple tr {
  display: table-row;
  vertical-align: inherit;
  border-color: inherit;
}
.qv-object-advanced-analytics-toolbox table.simple th:last-child:not(:first-child),
.qv-object-advanced-analytics-toolbox table.simple td:last-child:not(:first-child) {
  border-right: 0;
}
.qv-object-advanced-analytics-toolbox table.simple th,
.qv-object-advanced-analytics-toolbox table.simple td {
  text-align: left;
  box-sizing: border-box;
  padding: 5px 8px;
  height: 20px;
  border-right: 1px solid #CCCCCC;
}
.qv-object-advanced-analytics-toolbox table.simple tr td,
.qv-object-advanced-analytics-toolbox table.simple tr th {
  border-bottom: 1px solid #CCCCCC;
}
.qv-object-advanced-analytics-toolbox a.dt-button.buttons-copy,
.qv-object-advanced-analytics-toolbox a.dt-button.buttons-csv,
.qv-object-advanced-analytics-toolbox a.dt-button.buttons-pdf,
.qv-object-advanced-analytics-toolbox a.dt-button.buttons-print,
.qv-object-advanced-analytics-toolbox a.dt-button.buttons-copy:hover,
.qv-object-advanced-analytics-toolbox a.dt-button.buttons-csv:hover,
.qv-object-advanced-analytics-toolbox a.dt-button.buttons-pdf:hover,
.qv-object-advanced-analytics-toolbox a.dt-button.buttons-print:hover {
  width: auto;
  height: 28px;
  padding: 0px 12px;
  border-width: 1px;
  text-align: center;
  background-color: rgba(0, 0, 0, 0);
  background-image: none;
  border-color: #b3b3b3;
  color: #595959;
  height: 30px;
  line-height: 30px;
}
.qv-object-advanced-analytics-toolbox a.dt-button.buttons-copy span,
.qv-object-advanced-analytics-toolbox a.dt-button.buttons-csv span,
.qv-object-advanced-analytics-toolbox a.dt-button.buttons-pdf span,
.qv-object-advanced-analytics-toolbox a.dt-button.buttons-print span,
.qv-object-advanced-analytics-toolbox a.dt-button.buttons-copy:hover span,
.qv-object-advanced-analytics-toolbox a.dt-button.buttons-csv:hover span,
.qv-object-advanced-analytics-toolbox a.dt-button.buttons-pdf:hover span,
.qv-object-advanced-analytics-toolbox a.dt-button.buttons-print:hover span {
  background-color: rgba(0, 0, 0, 0);
  width: auto;
  height: 28px;
}
.qv-object-advanced-analytics-toolbox a.dt-button.buttons-copy span.lui-button__text,
.qv-object-advanced-analytics-toolbox a.dt-button.buttons-csv span.lui-button__text,
.qv-object-advanced-analytics-toolbox a.dt-button.buttons-pdf span.lui-button__text,
.qv-object-advanced-analytics-toolbox a.dt-button.buttons-print span.lui-button__text,
.qv-object-advanced-analytics-toolbox a.dt-button.buttons-copy:hover span.lui-button__text,
.qv-object-advanced-analytics-toolbox a.dt-button.buttons-csv:hover span.lui-button__text,
.qv-object-advanced-analytics-toolbox a.dt-button.buttons-pdf:hover span.lui-button__text,
.qv-object-advanced-analytics-toolbox a.dt-button.buttons-print:hover span.lui-button__text {
  padding: 0px 5px 0px 0px;
  font-family: "Noto Sans", sans-serif;
  font-weight: bold;
  font-size: 13px;
}
.qv-object-advanced-analytics-toolbox a.dt-button.buttons-copy:hover,
.qv-object-advanced-analytics-toolbox a.dt-button.buttons-csv:hover,
.qv-object-advanced-analytics-toolbox a.dt-button.buttons-pdf:hover,
.qv-object-advanced-analytics-toolbox a.dt-button.buttons-print:hover {
  border-color: #595959;
}
.qv-object-advanced-analytics-toolbox div.dataTables_filter label {
  font-family: "Noto Sans", sans-serif;
  font-weight: bold;
  font-size: 13px;
}
.qv-object-advanced-analytics-toolbox table.dataTable tr.even.selected,
.qv-object-advanced-analytics-toolbox table.dataTable tr.odd.selected,
.qv-object-advanced-analytics-toolbox table.dataTable tr.even.selected:hover,
.qv-object-advanced-analytics-toolbox table.dataTable tr.odd.selected:hover {
  background-color: #6CB33F !important;
}
.qv-object-advanced-analytics-toolbox .node circle {
  fill: #fff;
  stroke-width: 3px;
}
.qv-object-advanced-analytics-toolbox .node text {
  font: 12px sans-serif;
  font-family: "Noto Sans", sans-serif;
}
.qv-object-advanced-analytics-toolbox .link {
  fill: none;
  stroke-width: 2px;
}
